<template>
  <div class="cropper-component">
    <div class="info-item">
      <div class="btn-box">
        <label class="btn" for="uploads"></label>
        <input
          type="file"
          id="uploads"
          :value="imgFile"
          style="position:absolute; clip:rect(0 0 0 0);width: 1px;"
          accept="image/png, image/jpeg, image/gif, image/jpg"
          @change="uploadImg($event, 1)"
        />

        <input type="button" class="operation-btn" value="+" title="" @click="changeScale(1)" />
        <input type="button" class="operation-btn" value="-" title="" @click="changeScale(-1)" />
        <input type="button" class="operation-btn" value="" title="" @click="rotateLeft" />
        <input type="button" class="operation-btn" value="" title="" @click="rotateRight" />
        <input type="button" class="operation-btn" value="" title="" @click="down('blob')" />
        <div class="btn" @click="finish('blob')"></div>
      </div>
      <div class="operation-box">
        <div class="cropper">
          <vueCropper
            ref="cropper"
            :img="option.img"
            :outputSize="option.size"
            :outputType="option.outputType"
            :info="option.info"
            :full="option.full"
            :canMove="option.canMove"
            :canMoveBox="option.canMoveBox"
            :original="option.original"
            :autoCrop="option.autoCrop"
            :autoCropWidth="option.autoCropWidth"
            :autoCropHeight="option.autoCropHeight"
            :fixedBox="option.fixedBox"
            @realTime="realTime"
            @imgLoad="imgLoad"
          ></vueCropper>
        </div>
      </div>
      <div class="preview-box">
        <div></div>
        <div :style="previews.div" class="preview">
          <img :src="previews.url" :style="previews.img" />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "cropper",
  data() {
    return {
      //
      crap: false,
      previews: {},
      option: {
        img: "iflens.com/image/user", // 
        info: true, // 
        outputSize: 1, // 0.1-1
        full: true, //  propsfull
        outputType: "png", // 
        canMove: false, // 
        original: false, // 
        canMoveBox: true, // 
        autoCrop: true, // 
        autoCropWidth: 150,
        autoCropHeight: 150,
        fixedBox: true // 
      },
      fileName: "", // 
      downImg: "#",
      imgFile: "",
      uploadImgRelaPath: "" // 
    };
  },
  props: ["uploadType"],
  methods: {
    // /
    changeScale(num) {
      num = num || 1;
      this.$refs.cropper.changeScale(num);
    },
    // 
    rotateLeft() {
      this.$refs.cropper.rotateLeft();
    },
    // 
    rotateRight() {
      this.$refs.cropper.rotateRight();
    },
    // 
    finish(type) {
      console.log("finish", type);
      let _this = this;
      let formData = new FormData();
      // 
      if (type === "blob") {
        this.$refs.cropper.getCropBlob(data => {
          let img = window.URL.createObjectURL(data);
          formData.append("multfile", data, _this.fileName);
          formData.append("operaType", this.uploadType);
          console.log(formData.get("multfile"));
          this.$http.post(this.$api.upload, formData, false).then(res => {
            if (res.status == 200) {
              this.$emit("upload");
            }
          });
          // this.$store
          //   .dispatch("UPLOAD_HEAD", formData)
          //   .then(res => {
          //     let data = res.data.data;
          //     this.$emit("upload", data);
          //     this.$message.success("");
          //   })
          //   .catch(err => {
          //     if (err.data) {
          //       this.$message.error(err.data.msg);
          //     } else {
          //       this.$message.error("");
          //     }
          //   });
        });
      } else {
        this.$refs.cropper.getCropData(data => {
          // this.model = true;
          // this.modelSrc = data;
        });
      }
    },
    // 
    realTime(data) {
      console.log("realTime");
      this.previews = data;
    },
    // 
    down(type) {
      console.log("down");
      var aLink = document.createElement("a");
      aLink.download = "author-img";
      if (type === "blob") {
        this.$refs.cropper.getCropBlob(data => {
          this.downImg = window.URL.createObjectURL(data);
          aLink.href = window.URL.createObjectURL(data);
          aLink.click();
        });
      } else {
        this.$refs.cropper.getCropData(data => {
          this.downImg = data;
          aLink.href = data;
          aLink.click();
        });
      }
    },
    // 
    uploadImg(e, num) {
      console.log("uploadImg");
      var _this = this;
      // 
      var file = e.target.files[0];
      _this.fileName = file.name;
      if (!/\.(gif|jpg|jpeg|png|bmp|GIF|JPG|PNG)$/.test(e.target.value)) {
        alert(".gif,jpeg,jpg,png,bmp");
        return false;
      }
      var reader = new FileReader();
      reader.onload = e => {
        let data;
        if (typeof e.target.result === "object") {
          // Array Bufferblob base64
          data = window.URL.createObjectURL(new Blob([e.target.result]));
        } else {
          data = e.target.result;
        }
        if (num === 1) {
          _this.option.img = data;
        } else if (num === 2) {
          _this.example2.img = data;
        }
      };
      // base64
      // reader.readAsDataURL(file)
      // blob
      reader.readAsArrayBuffer(file);
    },
    imgLoad(msg) {
      console.log("imgLoad");
      console.log(msg);
    }
  }
};
</script>
<style scoped lang="scss">
.cropper-component {
  width: 500px;
  margin: 0 auto;
  position: relative;
  .btn-box {
    margin: 20px 0;
    .btn {
      padding: 4px 12px;
      text-align: center;
      border-radius: 4px;
      background-color: #387ef6;
      color: #fff;
      cursor: pointer;
      display: inline-block;
    }
    .operation-btn {
      width: 30px;
      height: 30px;
      line-height: 30px;
      text-align: center;
      background-color: #fff;
      border: 1px solid #eaeaea;
      cursor: pointer;
      display: inline-block;
      font-size: 20px;
      color: #333;
      padding: 0;
      margin: 0 10px;
    }
  }
  .info-item {
    .operation-box {
      display: inline-block;
      .cropper {
        width: 260px;
        height: 260px;
      }
    }
    .preview-box {
      top: 60px;
      right: 10px;
      float: right;
      width: 150px;
      .preview {
        width: 150px;
        height: 150px;
        border-radius: 50%;
        border: 1px solid #ccc;
        background-color: #ccc;
        margin: 5px;
        overflow: hidden;
      }
    }
  }
}
</style>



