import axios from 'axios';
import layer from 'layui-layer'
import qs from 'qs'
import store from '../store';

import router from '../router';
let localhosts = '/api';   //
//(token)
axios.interceptors.request.use(config => {
    // tokenhttp headertoken
    if (localStorage.JWT_TOKEN) {
        config.headers.token = localStorage.JWT_TOKEN;
    }
    return config;
}, error => {
    layer.msg(error, { icon: 5 })
    return Promise.reject(error);
});


axios.interceptors.response.use(response => {//
    return response.data
}, error => {
    layer.msg('Http', { icon: 5 });
    return Promise.reject(error.response);
});


function successfun(res) {//200
    if (res.status === 200) {
        if (res.message) {
            layer.msg(res.message, { icon: 1 });
        }
        return res

    } else {
        if (res.message) {
            layer.msg(res.message, { icon: 3 });
        }

        return res;
    }
}


function errorfun(res) {
    if (res.status != 200) {
        layer.msg(res.data.message, { icon: 3 }, function () {
            if (res.data.message == "Token has expired") {
                store.commit("tokenExpried")
            }
        }, { shade: [0.8] });
        return res;
    }
}


export default {
    post(url, data, stringify = true) {//post
        stringify ? data = qs.stringify(data) : data;
        return axios({
            method: 'post',
            baseURL: localhosts,
            url,
            data: data,
            withCredentials: true,
            timeout: 5000,//
        }).then(res => {

            return successfun(res)
        }, err => {
            return errorfun(err);
        })
    },


    get(url, params) {//get
        return axios({
            method: 'get',
            baseURL: localhosts,
            url,
            params,
            withCredentials: true,
            timeout: 5000,
        }).then(res => {
            return successfun(res)
        }, err => {
            return errorfun(err)
        })
    }

}


